import { useCallback, useState, useEffect } from 'react';
import { RiSearchLine } from 'react-icons/ri';
import { DatePicker } from 'antd';
import AppLayout from 'component/AppLayout';
import ResultText from 'component/ResultText';
import SkeletonItem from 'component/SkeletonItem';
import NoData from 'component/NoData';
import { tempRelationSearchRelations, tempRelationSearchEntities, tempRelationSearchDocuments } from 'util/tempData';

const { RangePicker } = DatePicker;

const dateRangeList = [
  { value: 'date-range-all', label: 'All (1970 ~ 2022)' },
  { value: 'date-range-1', label: 'past 1 year' },
  { value: 'date-range-5', label: 'past 5 years' },
  { value: 'date-range-10', label: 'past 10 years' },
  { value: 'date-range-custom', label: 'custom range', detail: true },
];

const relationTypeList = [
  { value: 'relation-type-any', label: 'Any relation type' },
  { value: 'relation-type-specific', label: 'Specific relations', detail: true },
];

const resultFilterList = [
  { value: 'result-filter-relation', label: 'relation-centered' },
  { value: 'result-filter-entity', label: 'entity-centered' },
];

const specificRelationList = [
  { value: 'specific-relation-ACTIVATOR', label: 'ACTIVATOR' },
  { value: 'specific-relation-AGONIST-ACTIVATOR', label: 'AGONIST-ACTIVATOR' },
  { value: 'specific-relation-AGONIST-INHIBITOR', label: 'AGONIST-INHIBITOR' },
  { value: 'specific-relation-ANTAGONIST', label: 'ANTAGONIST' },
  { value: 'specific-relation-DOWNREGULATOR', label: 'DOWNREGULATOR' },
  { value: 'specific-relation-REGULATOR', label: 'REGULATOR' },
  { value: 'specific-relation-INDIRECT-DOWNREGULATOR', label: 'INDIRECT-DOWNREGULATOR' },
  { value: 'specific-relation-INDIRECT-UPREGULATOR', label: 'INDIRECT-UPREGULATOR' },
  { value: 'specific-relation-INHIBITOR', label: 'INHIBITOR' },
  { value: 'specific-relation-PRODUCT-OF', label: 'PRODUCT-OF' },
  { value: 'specific-relation-SUBSTRATE', label: 'SUBSTRATE' },
  { value: 'specific-relation-SUBSTRATE_PRODUCT-OF', label: 'SUBSTRATE_PRODUCT-OF' },
  { value: 'specific-relation-UPREGULATOR', label: 'UPREGULATOR' },
  { value: 'specific-relation-COFACTOR', label: 'COFACTOR' },
  { value: 'specific-relation-DIRECT-REGULATOR', label: 'DIRECT-REGULATOR' },
  { value: 'specific-relation-MODULATOR', label: 'MODULATOR' },
  { value: 'specific-relation-MODULATOR-ACTIVATOR', label: 'MODULATOR-ACTIVATOR' },
  { value: 'specific-relation-MODULATOR-INHIBITOR', label: 'MODULATOR-INHIBITOR' },
  { value: 'specific-relation-PART-OF', label: 'PART-OF' },
  { value: 'specific-relation-DDI-advice', label: 'DDI-advice' },
  { value: 'specific-relation-DDI-effect', label: 'DDI-effect' },
  { value: 'specific-relation-DDI-mechanism', label: 'DDI-mechanism' },
  { value: 'specific-relation-DDI-int', label: 'DDI-int' },
];

const entityTypeList = [
  { value: 'entity-type-any', label: 'Any entity type' },
  { value: 'entity-type-specific', label: 'Specific entities', detail: true },
];

const specificEntityList = [
  { value: 'specific-entity-gene-protein', label: 'Gene-protein' },
  { value: 'specific-entity-chemical-compound', label: 'Chemical-compound' },
  { value: 'specific-entity-disease', label: 'Disease' },
  { value: 'specific-entity-mesh', label: 'MeSH' },
];

const RelationSearch = () => {
  const [keywordInput, setKeywordInput] = useState('');

  const [dateRangeRadio, setDateRangeRadio] = useState(dateRangeList[0].value);

  const [openDateRangeDetail, setOpenDateRangeDetail] = useState(false);

  const [includeInput, setIncludeInput] = useState('');

  const [excludeInput, setExcludeInput] = useState('');

  const [resultFiterRadio, setResultFiterRadio] = useState(resultFilterList[0].value);
  
  const [relationTypeRadio, setRelationTypeRadio] = useState(relationTypeList[0].value);

  const [openSpecificRelationDetail, setOpenSpecificRelationDetail] = useState(false);

  const [specificRelationCheck, setSpecificRelationCheck] = useState(new Set());

  const onChangeSpecificRelationCheck = useCallback((value) => {
    let newState = new Set(specificRelationCheck);

    if (newState.has(value)) {
      newState.delete(value);
    } else {
      newState.add(value);
    }

    setSpecificRelationCheck(newState);
  }, [specificRelationCheck]);

  const [entityTypeRadio, setEntityTypeRadio] = useState(entityTypeList[0].value);

  const [openSpecificEntityDetail, setOpenSpecificEntityDetail] = useState(false);

  const [specificEntityCheck, setSpecificEntityCheck] = useState(new Set());

  const onChangeSpecificEntityCheck = useCallback((value) => {
    let newState = new Set(specificEntityCheck);

    if (newState.has(value)) {
      newState.delete(value);
    } else {
      newState.add(value);
    }

    setSpecificEntityCheck(newState);
  }, [specificEntityCheck]);

  const [loadingRelations, setLoadingRelations] = useState(false);

  const [loadingEntities, setLoadingEntities] = useState(false);

  const [loadingDocuments, setLoadingDocuments] = useState(false);

  const [resultRelations, setResultRelations] = useState(null);

  const [resultEntities, setResultEntities] = useState(null);

  const [resultDocuments, setResultDocuments] = useState(null);

  const onSumbit = useCallback((e) => {
    e.preventDefault();

    setResultRelations(null);
    setResultEntities(null);
    setResultDocuments(null);
    setLoadingRelations(true);
    setLoadingEntities(true);
    setLoadingDocuments(true);

    //  
    setTimeout(() => {
      setResultRelations(tempRelationSearchRelations);
      setLoadingRelations(false);
    }, 2000);

    //  
    setTimeout(() => {
      setResultEntities(tempRelationSearchEntities);
      setLoadingEntities(false);
    }, 4000);

    //  
    setTimeout(() => {
      setResultDocuments(tempRelationSearchDocuments);
      setLoadingDocuments(false);
    }, 6000);
  }, []);

  const [activeRelation, setActiveRelation] = useState('');

  //  relation active
  useEffect(() => {
    if (resultRelations?.length > 0) {
      setActiveRelation(resultRelations[0]?.relation);
    }
  }, [resultRelations]);
  
  const onClickRelation = useCallback((relation) => {
    setActiveRelation(relation);
    setResultEntities(null);
    setResultDocuments(null);
    setLoadingEntities(true);
    setLoadingDocuments(true);

    //  
    setTimeout(() => {
      setResultEntities(tempRelationSearchEntities);
      setLoadingEntities(false);
    }, 2000);

    //  
    setTimeout(() => {
      setResultDocuments(tempRelationSearchDocuments);
      setLoadingDocuments(false);
    }, 4000);
  }, []);

  const [activeEntity, setActiveEntity] = useState('');

  //  relation active
  useEffect(() => {
    if (resultEntities?.length > 0) {
      setActiveEntity(resultEntities[0]?.entity);
    }
  }, [resultEntities]);
  
  const onClickEntity = useCallback((entity) => {
    setActiveEntity(entity);
    setResultDocuments(null);
    setLoadingDocuments(true);

    //  
    setTimeout(() => {
      setResultDocuments(tempRelationSearchDocuments);
      setLoadingDocuments(false);
    }, 2000);
  }, []);

  return (
    <AppLayout className="common-search-layout ett-search">
      <form className="common-form" onSubmit={onSumbit}>
        <div className="common-inner">
          <div className="form-area">
            <div className="form-keyword">
              <input 
                type="text" 
                className="vinput full form-keyword-input" 
                value={keywordInput} 
                onChange={(e) => setKeywordInput(e.target.value)} 
                placeholder=" Enter a keyword such as gene, protein, chemical-compound, disease, MeSH and so on to search for the most associated (relation, entity)" 
                required
              />
              <RiSearchLine className="form-keyword-icon" size={24} />
            </div>
            <div className="form-filter">
              <div className="form-filter-title">
                <p>Search Filter</p>
              </div>
              <div className="form-filter-row">
                <label className="form-filter-label">Date Range</label>
                <div className="form-filter-radio-row">
                  {dateRangeList.map((v, i) => (
                    <div key={i} className={`vradio form-filter-radio ${v.detail ? 'type-detail' : ''} ${(v.detail && openDateRangeDetail) ? 'active-detail' : ''}`}>
                      <input 
                        type="radio" 
                        id={v.value} 
                        value={v.value} 
                        name="date-range" 
                        checked={v.value === dateRangeRadio ? true : false} 
                        onChange={(e) => setDateRangeRadio(e.target.value)} 
                        onClick={() => v.value === 'date-range-custom' ? setOpenDateRangeDetail(prev => !prev) : setOpenDateRangeDetail(false)}
                      />
                      <label htmlFor={v.value}>{v.label}</label>
                    </div>
                  ))}
                </div>
              </div>
              {openDateRangeDetail && (
                <div className="form-filter-row">
                  <label className="form-filter-label">&nbsp;</label>
                  <div>
                    <RangePicker className="form-filter-date-range" picker="month" />
                  </div>
                </div>
              )}
              <div className="form-filter-row">
                <label className="form-filter-label">Include</label>
                <input 
                  type="text" 
                  className="vinput form-filter-input" 
                  value={includeInput} 
                  onChange={(e) => setIncludeInput(e.target.value)} 
                  placeholder="Enter keywords separated by comma (means OR) which should be included" 
                />
              </div>
              <div className="form-filter-row">
                <label className="form-filter-label">Exclude</label>
                <input 
                  type="text" 
                  className="vinput form-filter-input" 
                  value={excludeInput} 
                  onChange={(e) => setExcludeInput(e.target.value)} 
                  placeholder="Enter keywords separated by comma (means OR) which should be excluded" 
                />
              </div>
            </div>
            <div className="form-filter">
              <div className="form-filter-title">
                <p>Result Filter</p>
                <div className="form-filter-radio-row">
                  {resultFilterList.map((v, i) => (
                    <div key={i} className="vradio form-filter-radio">
                      <input 
                        type="radio" 
                        id={v.value} 
                        value={v.value} 
                        name="result-filter" 
                        checked={v.value === resultFiterRadio ? true : false} 
                        onChange={(e) => setResultFiterRadio(e.target.value)}
                      />
                      <label htmlFor={v.value}>{v.label}</label>
                    </div>
                  ))}
                </div>
              </div>
              <div className="form-filter-row">
                <label className="form-filter-label">Relation Type</label>
                <div className="form-filter-radio-row">
                  {relationTypeList.map((v, i) => (
                    <div key={i} className={`vradio form-filter-radio ${v.detail ? 'type-detail' : ''} ${(v.detail && openSpecificRelationDetail) ? 'active-detail' : ''}`}>
                      <input 
                        type="radio" 
                        id={v.value} 
                        value={v.value} 
                        name="relation-type" 
                        checked={v.value === relationTypeRadio ? true : false} 
                        onChange={(e) => setRelationTypeRadio(e.target.value)} 
                        onClick={() => {
                          if (v.value === 'relation-type-specific') {
                            setOpenSpecificRelationDetail(prev => !prev);
                          } else {
                            setOpenSpecificRelationDetail(false);
                            setSpecificRelationCheck(new Set());
                          }
                        }}
                      />
                      <label htmlFor={v.value}>{v.label}</label>
                    </div>
                  ))}
                </div>
              </div>
              {openSpecificRelationDetail && (
                <div className="form-filter-row">
                  <label className="form-filter-label">&nbsp;</label>
                  <div className="form-filter-check-row">
                    {specificRelationList.map((v, i) => (
                      <div key={i} className="vcheck form-filter-check">
                        <input 
                          type="checkbox" 
                          id={v.value} 
                          name="specific-relation"
                          checked={specificRelationCheck.has(v.value) ? true : false} 
                          onChange={() => onChangeSpecificRelationCheck(v.value)}
                        />
                        <label htmlFor={v.value}>{v.label}</label>
                      </div>
                    ))}
                  </div>
                </div>
              )}
              <div className="form-filter-row">
                <label className="form-filter-label">Entity Type</label>
                <div className="form-filter-radio-row">
                  {entityTypeList.map((v, i) => (
                    <div key={i} className={`vradio form-filter-radio ${v.detail ? 'type-detail' : ''} ${(v.detail && openSpecificEntityDetail) ? 'active-detail' : ''}`}>
                      <input 
                        type="radio" 
                        id={v.value} 
                        value={v.value} 
                        name="entity-type" 
                        checked={v.value === entityTypeRadio ? true : false} 
                        onChange={(e) => setEntityTypeRadio(e.target.value)} 
                        onClick={() => {
                          if (v.value === 'entity-type-specific') {
                            setOpenSpecificEntityDetail(prev => !prev);
                          } else {
                            setOpenSpecificEntityDetail(false);
                            setSpecificEntityCheck(new Set());
                          }
                        }}
                      />
                      <label htmlFor={v.value}>{v.label}</label>
                    </div>
                  ))}
                </div>
              </div>
              {openSpecificEntityDetail && (
                <div className="form-filter-row">
                  <label className="form-filter-label">&nbsp;</label>
                  <div className="form-filter-check-row">
                    {specificEntityList.map((v, i) => (
                      <div key={i} className="vcheck form-filter-check">
                        <input 
                          type="checkbox" 
                          id={v.value} 
                          name="specific-entity"
                          checked={specificEntityCheck.has(v.value) ? true : false} 
                          onChange={() => onChangeSpecificEntityCheck(v.value)}
                        />
                        <label htmlFor={v.value}>{v.label}</label>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
            <div className="form-button-row">
              <button type="submit" className={`vbutton form-button-submit ${(loadingRelations || loadingDocuments) ? 'loading' : ''}`}>Apply</button>
            </div>
          </div>
        </div>
      </form>
      <section className="common-result">
        <div className="common-inner">
          <div className="result-area result-area2">
            <div className="result-col1">
              {loadingRelations && <SkeletonItem line={3} />}
              {resultRelations && (
                <table className="common-table result-table">
                  <colgroup>
                    <col width="" />
                    <col width="" />
                    <col width="" />
                  </colgroup>
                  <thead>
                    <tr className="text-center">
                      <th>Relations</th>
                      <th>Freq</th>
                      <th>Category</th>
                    </tr>
                  </thead>
                  <tbody>
                    {resultRelations?.length > 0 ? (
                      resultRelations?.map((v, i) => (
                        <tr key={i} className={activeRelation === v.relation ? 'active' : ''} onClick={() => onClickRelation(v.relation)}>
                          <td>{v.relation}</td>
                          <td className="text-right">{v.freq}</td>
                          <td className="text-center">{v.category}</td>
                        </tr>
                      ))
                    ) : (
                      <tr className="no-data">
                        <td colSpan={3}>
                          <NoData />
                        </td>
                      </tr>
                    )}
                  </tbody>
                </table>
              )}
            </div>
            <div className="result-col2">
              {loadingEntities && <SkeletonItem line={3} />}
              {resultEntities && (
                <table className="common-table result-table">
                  <colgroup>
                    <col width="" />
                    <col width="" />
                    <col width="" />
                  </colgroup>
                  <thead>
                    <tr className="text-center">
                      <th>Entities</th>
                      <th>Freq</th>
                      <th>Category</th>
                    </tr>
                  </thead>
                  <tbody>
                    {resultEntities?.length > 0 ? (
                      resultEntities?.map((v, i) => (
                        <tr key={i} className={activeEntity === v.entity ? 'active' : ''} onClick={() => onClickEntity(v.entity)}>
                          <td>{v.entity}</td>
                          <td className="text-right">{v.freq}</td>
                          <td className="text-center">{v.category}</td>
                        </tr>
                      ))
                    ) : (
                      <tr className="no-data">
                        <td colSpan={3}>
                          <NoData />
                        </td>
                      </tr>
                    )}
                  </tbody>
                </table>
              )}
            </div>
            <div className="result-col3">
              {loadingDocuments && <SkeletonItem line={3} /> }
              {resultDocuments && (
                <div className="result-document">
                  <div className="result-document-title">
                    <p>Documents</p>
                  </div>
                  <ul className="common-document-list">
                    {resultDocuments?.length > 0 ? (
                      resultDocuments?.map((v, i) => (
                        <li key={i}>
                          <div className="list-title">
                            <a href={v.url} target="_blank" rel="noreferrer">{v.title}</a>
                          </div>
                          <ResultText text={v.text} />
                        </li>
                      ))
                    ) : (
                      <li>
                        <NoData />
                      </li>
                    )}
                  </ul>
                </div>
              )}
            </div>
          </div>
        </div>
      </section>
    </AppLayout>
  );
};

export default RelationSearch;
